<template>
    <div class="page-approve page">
        <div @click="goPublish" class="publish-btn">
            <p>{{$t('publish.publish')}}</p>
        </div>
        <div class="header bg">
            <div class="page-approve__hint">
                <img src="../../assets/publish/warning.png" alt="">
                <p>{{$t('publish.toastTop')}}</p>
            </div>
            <div class="page-approve__content">
                <h3 class="page-approve__title">{{$t('publish.title')}}</h3>
                <ul>
                    <li>
                        <h4>{{$t('publish.member')}}</h4>
                        <p>{{$t('publish.memberText')}}</p>
                    </li>
                    <li>
                        <h4>{{$t('publish.concat')}}</h4>
                        <p>{{$t('publish.concatText')}}</p>
                    </li>
                    <li>
                        <h4>{{$t('publish.transaction')}}</h4>
                        <p>{{$t('publish.transactionText')}}</p>
                    </li>
                </ul>
            </div>
        </div>
        <div class="page-approve__login bg">
            <h3 class="page-approve__title">{{$t('publish.account')}}</h3>
            <ul>
                <li>
                    <h4>{{$t('publish.examine')}}</h4>
                    <img :src="$t('img.pic1')" alt="">
                    <div class="apply-btn" @click="getApply">{{$t('publish.apply')}}</div>
                </li>
                <li>
                    <h4>{{$t('publish.active')}}</h4>
                    <img :src="$t('img.pic2')" alt="">
                </li>
                <li>
                    <h4>{{$t('publish.goDomian')}}</h4>
                    <img :src="$t('img.pic3')" alt="">
                </li>
                <li>
                    <h4>{{$t('publish.complete')}}</h4>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';
    import config from '../../config.js';
    import { mapState } from 'vuex';
    export default {
        name: 'approve',
        computed: {
            ...mapState({
                personalData: state => state.personal.personalData
            })
        },
        created () {
            this.$router.setTitle(this.$t('title.approve'));
        },
        methods: {
            getApply () {
                let params = {},
                    self = this;
                params.key = 'domain_application';
                publicPost(getApiUrl('getAll'), params, {loading: true}).then((res) => {
                    window.location.href = res[0].configValue;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            },
            // 去发布
            goPublish () {
                if (config.userList.length > 1) {
                    this.$router.push({
                        path: '/edit-account',
                        query: {
                            isDependable: 2,
                            userId: this.personalData && this.personalData.userId ? this.personalData.userId : ''
                        }
                    });
                } else {
                    this.$router.push({
                        path: '/istrust-publish',
                        query: {
                            userId: config.userList[0]
                        }
                    });
                }
            }
        }
    };
</script>

